import React from 'react'

function Section1() {
  return (
    <div>
      <div className="header">Spotings Appointment Sheduler</div>
      <div className="tracker">
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div className="time">
        <h2>select date for appointment</h2>
        <div className="start">
          <div>
            <span>from</span>
            <input type="date" name="fromDate"/>
          </div>
          <div>
            <span>time</span>
            <input type="time" name="timeFrom" id=""/>
          </div>
        </div>
        <div className="to">
          <div>
            <span>to</span>
            <input type="date" name="fromDate"/>
          </div>
          <div>
            <span>time</span>
            <input type="time" name="timeTo" id=""/>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Section1
